.body {
    display: flex;
    justify-content: center;
    position: absolute;
    height: 94%;
    width: 100%;
    padding-top: 10px;
}
.body>div>img{
    width: 100%;
    height: 100%;
}
.bodyBlock{
    display: flex;
    margin-left: auto;
    flex-direction: column;
    margin: auto;
    align-items: center;
    justify-content: center;
}
.bodyBlockLinks{
    display: flex;
    margin-left: auto;
    flex-direction: column;
    align-items: center;
}
.h{
    font-size: 70px;
    font-weight: bolder;
    text-align:  left;

}
.message{
    padding-top: 30px;
    font-size: 35px;
    font-weight: normal;
    text-align:  left;
}
.error404{
    font-size: 200px;
    font-weight: bolder;
    text-align: center;
    text-shadow: -1px 0 black, 0 1px white, 8px 0 red, 0 -1px black;
    letter-spacing: 25px;
}